import React from "react"
import { Link, graphql } from "gatsby"
import Img from "gatsby-image"
import { css } from "@emotion/core"
import styled from "@emotion/styled"

import Layout from "../components/layout"
import SEO from "../components/seo"
import '../components/global.css'
// import Img from 'gatsby-image';

const PageTitle = styled.h1`
  font-family: 'DM Sans', sans-serif;
  font-weight: 500;
`

const Content = styled.div`
  margin: 0 auto;
  max-width: 860px;
  padding: 1.45rem 1.0875rem;
`

const ArticleDate = styled.h5`
  display: inline;
  // color: #606060;
  // color: #063268;
  // color: #c42a00;
  // color: rgb(143, 10, 93);
  color: #fb2811;
  margin-bottom: 10px;
`

const MarkerHeader = styled.h3`
  display: inline;
  font-family: 'DM Sans', sans-serif;
  font-weight: 500;
  // border-radius: 1em 0 1em 0;
  margin-bottom: 10px;
  // background-image: linear-gradient(
  //   -100deg,
  //   rgba(255, 250, 150, 0.15),
  //   rgba(255, 250, 150, 0.8) 100%,
  //   rgba(255, 250, 150, 0.25)
  // );

  // background-color: rgba(92, 187, 254, 0.45);
  // height: 3px;
  // background-color: rgba(216, 123, 73, 0.5);
  // padding-right: 9px;
  // padding-left: 7px;
  `

const ReadingTime = styled.h5`
  display: inline;
  // color: #606060;
  // color: #063268;
  // color: #c42a00;
  // color: rgb(240, 50, 50);
  // color: rgb(143, 10, 93);
  color: #fb2811;
  margin-bottom: 10px;
`

const SinglePost = styled.section`
  // padding: 0.3em 0em 0.3em 0.7em;
  // border-left: 3px solid #4c85ba;
  // border-radius: 0.4em 0.4em 0.4em 0.4em;
`;

const IndexPage = ({ data }) => {
  return (
    <Layout>
      <SEO title="Blog" />
      <Content>
        <PageTitle>Blog</PageTitle>
        {data.allMarkdownRemark.edges.map(({ node }) => (
          <SinglePost>
            <div key={node.id}>
            {/* // <div key={node.id}> */}
              <Link
                to={node.frontmatter.path}
                css={css`
                  text-decoration: none;
                  color: inherit;
                `}
              >
                {/* {node.frontmatter.image.childImageSharp.fluid} */}
                {/* <div>{node.frontmatter.image}</div> */}
                <MarkerHeader>{node.frontmatter.title} </MarkerHeader>
                <div>
                  <ArticleDate>{node.frontmatter.date}</ArticleDate>
                  <ReadingTime> - {node.fields.readingTime.text}</ReadingTime>
                </div>
                <div>{node.frontmatter.description}</div>
                {/* <div><Img fluid={node.frontmatter.thumbnail.childImageSharp}/></div> */}
              </Link>
            </div>
          </SinglePost>
        ))}
      </Content>
    </Layout>
  )
}

export default IndexPage

export const query = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
      totalCount
      edges {
        node {
          id
          frontmatter {
            title
            date(formatString: "DD MMMM, YYYY")
            path
            description

          }
          fields {
            slug
            readingTime {
              text
            }
          }
          excerpt
        }
      }
    }
  }
`
